.aboutme{
    text-align: justify;
}

.home-container{
    position: relative;
    min-height: 100vh;
    padding: 50px 40px;
    margin-top: 7px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.home-container .logo{
    position: inherit;
    margin-top: -110px;
    height: 300px;
    width: 520px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    user-select: none;
}

.home-container .logo .main-img{
    position: absolute;
    height: 300px;
    width: 300px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;
    cursor: pointer;
}
.home-container .logo:hover{
    filter: brightness(50%);
    cursor: pointer;
}
.home-container .logo .main-img.active{
    animation: spin 8s linear 1s infinite;
}

@keyframes spin{
    to{
        transform: rotate(360deg);
    }
}

.home-container .logo .circle{
    position: absolute;
    height: 100px;
    width: 100px;
    border-radius: 50%;
    background-color: rgb(4, 4, 44);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.2s ease;
    opacity: 0;
}

.home-container .logo .main-img.active .circle{
    opacity: 1;
}

.home-container .logo .main-img.active .circle:nth-child(1){
    background: url(../props/html.png);
    background-size: cover;
    background-position: center;
    position: absolute;
    transform: translateY(-210%);
}

