.home-container img{
    position: absolute;
    height: 200px;
    width: 200px;
    border-radius: 50%;
    border: 3px solid rgb(108, 161, 197);
    transform: translate(-200px);
    opacity: 0;
    animation: fade-in 1s ease 1 0.5s forwards;
}

@keyframes fade-in{
    to{
        transform: translate(0);
        opacity: 1;
    }
}

.home-container h2{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 60%;
    margin-top: 20px;
}

.home-container h2 span{
    font-size: 25px;
    opacity: 0.8;
    padding-bottom: 5px;
    border-bottom: 2px solid var(--color-pri);
    font-family: 'Open Sans Condensed', sans-serif;
}

.home-container h2 p{
    font-size: 20px;
    opacity: 1;
    font-weight: 100;
    margin-top: 1px;
    font-family: 'Open Sans', sans-serif;
}

.scroll-down{
    transform: rotate(90deg);
    position: absolute;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    cursor: pointer;
}

.scroll-down hr{
    background-color: blue;
    width: 20px;
}

.scroll-down h5{
    font-size: 18px;
    text-transform: capitalize;
    font-weight: 100;
}

.scroll-down .scroll{
    transform: rotate(-90deg);
    font-weight: 100;
}