.footer-container{
    position: relative;
    display:flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    min-height: 10vh;
    margin-top: 80px;
    margin-bottom: 80px;
}

.footer-container h1{
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 100;
    font-size: 25px;
    margin: 0 auto;
    text-align: center;
    text-transform: capitalize;
}

.footer-container h2{
    font-family: 'Open Sans Condensed';
    font-weight: 100;
    font-size: 20px;
    margin-top: 15px;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5px;
    padding: 10px 20px;
    border-radius: 5px;
}

.footer-container h2:hover{
    background-color: rgba(0, 0, 0, 0.062);
}

.social-links{
    padding: 10px;
    margin: 20px auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.social-links .social{
    height: 45px;
    width: 45px;
    background-color: rgba(0, 0, 0, 0.548);
    margin: 0 5px;
    padding: 10px;
    backdrop-filter: blur(10px);
    cursor: pointer;
    transition: background .2s ease;
}

.social-links .social:hover{
    background: rgba(255, 255, 255, 0.363);
    fill: var(--color-pri);
}